<template>
  <v-container fluid fill-height style="padding:0;" id="databuffer" grid-list-md>
    <Blog :items="items" :time="time">
      <div class="blogcontent">
        <v-layout row wrap>
          <v-flex sm12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="arraybuffer">ArrayBuffer</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <p class=".body-1">1. <code>ArrayBuffer</code>表示的是一个固定长度的二进制缓冲区，也就是二进制数组</p>
                    <p class=".body-1">2. <code>ArrayBuffer</code>是一个相当抽象的模型，不能直接操作数组中存的内容，可以当做<code>DataView</code>的参数来使用</p>
                    <table>
                        <colgroup>
                            <col span="2"> <!-- 不写span属性默认就是1 -->
                            <col span="1" style="min-width:300px;">
                            <col span="1" style="min-width:200px;">
                            <col span="1" style="min-width:100px;">
                            <col span="1" style="min-width:300px;">
                        </colgroup>
                        <tr>
                            <td></td>
                            <th>属性/方法名</th>
                            <th>测试数据</th>
                            <th>示例</th>
                            <th>输出</th>
                            <th>说明</th>
                            <th>备注</th>
                        </tr>
                        <tr>
                            <th>1</th>
                            <td>byteLength</td>
                            <td>buffer = new ArrayBuffer(32);</td>
                            <td>buffer.byteLength</td>
                            <td>32</td>
                            <td>获取buffer的长度</td>
                            <td>ES6</td>
                        </tr>
                        <tr>
                            <th>2</th>
                            <td>prototype</td>
                            <td colspan="2"><pre>{{pres[0]}}</pre></td>
                            <td>'ok'</td>
                            <td>prototype可以获取ArrayBuffer的原型对象，在原型对象上面定义全局的方法和属性</td>
                            <td>ES6</td>
                        </tr>
                        <tr>
                            <th>3</th>
                            <td>isView</td>
                            <td colspan="2"><pre>{{pres[1]}}</pre></td>
                            <td>true true</td>
                            <td>判断传入的值是否是一个buffer</td>
                            <td>ES6</td>
                        </tr>
                    </table>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
          <v-flex sm12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="dataview">DataView</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <p class=".body-1">1. <code>DataView</code>是操作<code>ArrayBuffer</code>的一个工具，它自身不是buffer</p>
                    <p class=".body-1">2. 使用<code>DataView</code>不需要考虑字节顺序（不同机器的字节顺序不同，跟机器的CPU型号有关，一般都是低位排序）</p>
                    <p class=".body-1">3. 基本使用如下</p>
                    <pre>
                        {{pres[2]}}
                    </pre>

                    <table>
                        <colgroup>
                            <col span="2">
                            <col span="1" style="min-width:300px;">
                            <col span="1" style="min-width:200px;">
                            <col span="1" style="min-width:100px;">
                            <col span="1" style="min-width:300px;">
                        </colgroup>
                        <tr>
                            <td></td>
                            <th>属性/方法名</th>
                            <th colspan="2">示例</th>
                            <th>输出</th>
                            <th>说明</th>
                            <th>备注</th>
                        </tr>
                        <tr>
                            <th>1</th>
                            <td>buffer</td>
                            <td colspan="2"><pre>{{pres[3]}}</pre></td>
                            <td>ArrayBuffer(32) {}</td>
                            <td>获取dataview引用的buffer</td>
                            <td>ES6</td>
                        </tr>
                        <tr>
                            <th>2</th>
                            <td>byteLength</td>
                            <td colspan="2"><pre>{{pres[4]}}</pre></td>
                            <td>4</td>
                            <td>获取dataview的字节长度</td>
                            <td>ES6</td>
                        </tr>
                        <tr>
                            <th>3</th>
                            <td>byteOffset</td>
                            <td colspan="2"><pre>{{pres[5]}}</pre></td>
                            <td>8</td>
                            <td>获取dataview的偏移的字节数</td>
                            <td>ES6</td>
                        </tr>
                        <tr>
                            <th>4</th>
                            <td>setInt8 / getInt8</td>
                            <td colspan="2"><pre>{{pres[6]}}</pre></td>
                            <td>-128<br>127</td>
                            <td>设置和获取8位整型数字，占用一个字节，数值范围<code>-2<sup>7</sup>~2<sup>7</sup>-1</code></td>
                            <td>ES6</td>
                        </tr>
                        <tr>
                            <th>5</th>
                            <td>setUint8 / getUint8</td>
                            <td colspan="2"><pre>{{pres[7]}}</pre></td>
                            <td>0<br>255</td>
                            <td>设置和获取8位无符号整型数字，占用一个字节，数值范围<code>0~2<sup>8</sup>-1</code></td>
                            <td>ES6</td>
                        </tr>
                        <tr>
                            <th>6</th>
                            <td>setInt16 / getInt16</td>
                            <td colspan="2"><pre>{{pres[8]}}</pre></td>
                            <td>-32768<br>32767</td>
                            <td>设置和获取16位整型数字，占用两个字节，数值范围<code>-2<sup>15</sup>~2<sup>15</sup>-1</code></td>
                            <td>ES6</td>
                        </tr>
                        <tr>
                            <th>7</th>
                            <td>setUint16 / setUint16</td>
                            <td colspan="2"><pre>{{pres[9]}}</pre></td>
                            <td>0<br>65535</td>
                            <td>设置和获取16位无符号整型数字，占用两个字节，数值范围<code>0~2<sup>16</sup>-1</code></td>
                            <td>ES6</td>
                        </tr>
                        <tr>
                            <th>8</th>
                            <td>setInt32 / getInt32</td>
                            <td colspan="2"><pre>{{pres[10]}}</pre></td>
                            <td>-2147483648<br>2147483647</td>
                            <td>设置和获取32位整型数字，占用四个字节，数值范围<code>-2<sup>31</sup>~2<sup>31</sup>-1</code></td>
                            <td>ES6</td>
                        </tr>
                        <tr>
                            <th>9</th>
                            <td>setUint32 / getUint32</td>
                            <td colspan="2"><pre>{{pres[11]}}</pre></td>
                            <td>0<br>4294967295</td>
                            <td>设置和获取32位无符号整型数字，占用四个字节，数值范围<code>0~2<sup>32</sup>-1</code></td>
                            <td>ES6</td>
                        </tr>
                        <tr>
                            <th>10</th>
                            <td>setFloat32 / getFloat32</td>
                            <td colspan="2"><pre>{{pres[12]}}</pre></td>
                            <td>1.100000023841858</td>
                            <td>设置和获取32位浮点数字，占用四个字节，数值范围<code>-3.40310<sup>38</sup>~3.40310<sup>38</sup></code></td>
                            <td>ES6</td>
                        </tr>
                        <tr>
                            <th>11</th>
                            <td>setFloat64 / getFloat64</td>
                            <td colspan="2"><pre>{{pres[13]}}</pre></td>
                            <td>1.1</td>	
                            <td>设置和获取32位浮点数字，占用八个字节，数值范围<code>-1.79810<sup>308</sup>~1.79810<sup>308</sup></code></td>
                            <td>ES6</td>
                        </tr>
                    </table>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
          <v-flex sm12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="typedarray">类型数组</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <p class=".body-1">1. 类型数组其实就是<code>DataView</code>应用的简写形式，将创建<code>ArrayBuffer</code>的过程省略了</p>
                    <p class=".body-1">2. ES6包含的所有类型数组如下
                        <pre>{{pres[14]}}</pre>
                        可见，这些类型数组分别对应<code>DataView</code>的那几个方法
                    </p>
                    <p class=".body-1">3. 下面以<code>Uint8Array</code>为例演示属性和方法，其他的类型数组都是通用的</p>
                    <ul>
                        声明数组的几种方式
                        <li>
                            let uint8 = new Uint8Array(); // 创建一个空的数组
                        </li>
                        <li>
                            let uint8 = new Uint8Array(8); // 创建能够包含8个元素的数组
                        </li>
                        <li>
                            let uint8 = new Uint8Array(new Int8Array([1,2,3,4])); // 复制其他数组的元素
                        </li>
                        <li>
                            let uint8 = new Uint8Array(new ArrayBuffer(32), 8, 4); // 类DataView的使用方式
                        </li>
                    </ul><br>
                    <ul>
                        几个属性
                        <li>
                            Uint8Array.BYTES_PER_ELEMENT; // 返回类型数组每个元素的占用字节数，都是常量
                        </li>
                        <li>
                            Uint8Array.name; // 返回数组的名字
                        </li>
                        <li>
                            Uint8Array.prototype; // 返回原型对象
                        </li>
                    </ul><br>
                    <ul>
                        方法，大部分方法和Array的用法一样的，上篇文章有详细的解释，这里具体就不赘述了
                        <li>from()</li>
                        <li>of()</li>
                        <li>copy​Within()</li>
                        <li>entries()</li>
                        <li>every()</li>
                        <li>fill()</li>
                        <li>filter()</li>
                        <li>find()</li>
                        <li>find​Index()</li>
                        <li>for​Each()</li>
                        <li>includes()</li>
                        <li>indexOf()</li>
                        <li>join()</li>
                        <li>keys()</li>
                        <li>last​IndexOf()</li>
                        <li>map()</li>
                        <li>reduce()</li>
                        <li>reduce​Right()</li>
                        <li>reverse()</li>
                        <li>set()
                            <pre>{{pres[15]}}</pre>
                        </li>
                        <li>slice()</li>
                        <li>some()</li>
                        <li>sort()</li>
                        <li>subarray()
                            <pre>{{pres[16]}}</pre>
                        </li>
                        <li>toLocale​String()</li>
                        <li>toString()</li>
                        <li>values()</li>
                        <li>[Symbol.iterator]()</li>
                    </ul>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
        </v-layout>
      </div>
    </Blog>
  </v-container>
</template>

<script>
import Blog from "../Blog.vue";
import { setInterval } from "timers";
export default {
  components: { Blog },
  data() {
    return {
      time: "2018-12-5",
      title: "二进制缓冲区",
      items: [
        {
          title: "ArrayBuffer",
          target: "#arraybuffer"
        },
        { divider: true },
        {
          title: "DataView",
          target: "#dataview"
        },
        { divider: true },
        {
          title: "类型数组",
          target: "#typedarray"
        }
      ],
      pres: 
[
    `
    ArrayBuffer.prototype.fn = function() {
        return 'ok';
    }
    const buffer = new ArrayBuffer(32);
    buffer.fn()
    `,
    `
    const buffer = new ArrayBuffer(32);
    const view = new DataView(buffer);
    const int8arr = new Int8Array(8);
    console.log(
        ArrayBuffer.isView(view), 
        ArrayBuffer.isView(int8arr)
    );
    `,
    `
    const bf = new ArrayBuffer(32); // 创建buffer
    const vw = new DataView(bf, 8, 4); // 将第8位字节后面跟着的4个字节划入此dataview管理范围
    vw.setInt8(0, 1); // int8占用1个字节，所以这里可以设置4个int8，多出就会报错
    vw.setInt8(1, 2);
    vw.setInt8(2, 3);
    vw.setInt8(3, 4);
    console.log(vw.getInt8(0)); // 1
    console.log(vw.getInt8(1)); // 2
    console.log(vw.getInt8(2)); // 3
    console.log(vw.getInt8(3)); // 4
    `,
    `
    const vw = new DataView(new ArrayBuffer(32));
    console.log(vw.buffer);
    `,
    `
    const vw = new DataView(new ArrayBuffer(32), 8, 4);
    console.log(vw.byteLength);
    `,
    `
    const vw = new DataView(new ArrayBuffer(32), 8, 4);
    console.log(vw.byteOffset);
    `,
    `
    const vw = new DataView(new ArrayBuffer(32));
    vw.setInt8(0, -128);
    vw.setInt8(1, 127);
    console.log(vw.getInt8(0));
    console.log(vw.getInt8(1));
    `,
    `
    const vw = new DataView(new ArrayBuffer(32));
    vw.setUint8(0, 0);
    vw.setUint8(1, 255);
    console.log(vw.getUint8(0));
    console.log(vw.getUint8(1));
    `,
    `
    const vw = new DataView(new ArrayBuffer(32), 0, 4);
    vw.setInt16(0, -32768);
    vw.setInt16(2, 32767);
    console.log(vw.getInt16(0));
    console.log(vw.getInt16(2));
    `,
    `
    const vw = new DataView(new ArrayBuffer(32), 0, 4);
    vw.setUint16(0, 0);
    vw.setUint16(2, 65535);
    console.log(vw.getUint16(0));
    console.log(vw.getUint16(2));
    `,
    `
    const vw = new DataView(new ArrayBuffer(32), 0, 8);
    vw.setInt32(0, -2147483648);
    vw.setInt32(4, 2147483647);
    console.log(vw.getInt32(0));
    console.log(vw.getInt32(4));
    `,
    `
    const vw = new DataView(new ArrayBuffer(32), 0, 8);
    vw.setUint32(0, 0);
    vw.setUint32(4, 4294967295);
    console.log(vw.getUint32(0));
    console.log(vw.getUint32(4));
    `,
    `
    const vw = new DataView(new ArrayBuffer(32), 0, 4);
    vw.setFloat32(0, 1.1);
    console.log(vw.getFloat32(0));
    `,
    `
    const vw = new DataView(new ArrayBuffer(32), 0, 8);
    vw.setFloat64(0, 1.1);
    console.log(vw.getFloat64(0));
    `,
    `
    Int8Array;
    Uint8Array;
    Uint8ClampedArray; // 这个类型和Uint8Array一样，区别就是 如果设置的值超出[0,255]的范围，则设置为0或255; 如果指定非整数，则将设置最接近的整数
    Int16Array;
    Uint16Array;
    Int32Array;
    Uint32Array;
    Float32Array;
    Float64Array;
    `,
    `
    let uint8 = new Uint8Array(3);
    uint8.set([1,2,3], 0); // 将数组元素设置到指定的索引位 [1, 2, 3]
    `,
    `
    let uint8 = new Uint8Array([1,2,3]);
    uint8.subarray(0,2); // 提供开始索引和结束索引来截取部分数组，不包括结束索引，不修改原始数组，[1, 2]
    // 这里生成的子数组和原始数字共用相同的内存空间，注意和slice的区别
    `
]
    };
  },
  mounted() {
  },
  methods: {}
};
</script>

<style lang="scss">
#databuffer {
  .blogcontent {
    padding-top: 20px;
  }
  .code {
    font-size: 12px;
    background-color: #f9f9f5;
    white-space: pre-wrap;
    word-wrap: break-word;
  }

  table {
    border-collapse: collapse;
    border: 2px solid rgb(100, 100, 100);
    letter-spacing: 1px;
    font-family: sans-serif;
    font-size: 0.7rem;
    min-width: 1150px;
  }

  td,
  th {
    border: 1px solid rgb(100, 100, 100);
    padding: 10px 10px;
  }

  td {
    text-align: left;
  }
}
</style>

